import kAdvantage from '@/components/k-ui/k-list/k-advantage.vue'
import {
	mapState
} from 'vuex';
export default {
	components: {
		kAdvantage
	},
	data() {
		return {
			data: [],
			// 添加
			addShow: false,
			advantageTitle: '',
			advantageContent: '',
			// 编辑
			editShow: false,
			modalName: null,
			curUserId: '',
			editId: ''
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
	},
	onLoad(e) {
		uni.showLoading({
			title: '正在加载'
		})
		this.curUserId = e.id
		console.log(e)
		this.loadData()
	},
	methods: {
		showModal(e) {
			console.log(e)
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
			this.advantageTitle = ""
			this.advantageContent = ""
		},
		edit(index){
			this.advantageTitle = this.data[index].name
			this.advantageContent = this.data[index].brief
			this.editId = this.data[index].id
			this.modalName = 'editModal'
		},
		async loadData(){
			const res = await this.$api.request({
				url: '/user/getAdvantage/' + this.curUserId
			})
			// console.log(res)
			this.data = res.data
			uni.hideLoading()
		},
		async addAdvantage(){
			if(this.advantageTitle == "" || this.advantageContent == ""){
				this.$api.msg("请完善信息~")
				return;
			}
			const res = await this.$api.request({
				url: '/user/addAdvantage',
				method: "PUT",
				contentType: "application/json",
				data: {
					name: this.advantageTitle,
					brief: this.advantageContent
				}
			})
			if(res.status == 200){
				this.$api.msg("添加成功~")
				this.loadData()
				this.hideModal()
			}
		},
		async delAdvantage(){
			const res = await this.$api.request({
				url: '/user/delAdvantage/' + this.editId
			})
			if(res.status == 200){
				this.$api.msg("删除成功~")
				this.loadData()
				this.hideModal()
			}
			
		},
		async editAdvantage(){
			if(this.advantageTitle == "" || this.advantageContent == ""){
				this.$api.msg("请完善信息")
				return;
			}
			const res = await this.$api.request({
				url: '/user/editAdvantage/' + this.editId,
				method: "PUT",
				contentType: "application/json",
				data: {
					name: this.advantageTitle,
					brief: this.advantageContent
				}
			})
			if(res.status == 200){
				this.$api.msg("修改成功~")
				this.loadData()
				this.hideModal()
			}
			
		}
	}
}
